<template>
    <div>
        <ul class="plugin-list">
            <li>
                <h3>Name</h3>
                <div class="text">PreLoader</div>
            </li>
            <li>
                <h3>Introduction</h3>
                <div class="text">
                    
                </div>
            </li>
            <li>
                <h3>Demo</h3>
                <p class="btn" @click.stop="onClickDemo()"></p>
                <p class="btn" @click.stop="onClickDemo2()"></p>
            </li>
            <li>
                <h3>Usage</h3>
<pre class="JS">
var content = '';//
mobileModal.showPreLoader(content);

</pre>
            </li>
            <li>
                <h3>Path</h3>
                <div class="text">
                    
                    'src/lib/mobileModal/mobileModal.css'
                    'src/lib/mobileModal/mobileModal.js'
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import mobileModal from '../../lib/mobileModal/mobileModal.js'
    export default {
        name: 'toast',
        data(){
            return {
                nowVal: ''
            }
        },
        methods: {
            onClickDemo(){
                mobileModal.showPreLoader('');
                setTimeout(function () {
                    mobileModal.hidePreLoader();
                }, 1000);
            },

            onClickDemo2(){
                mobileModal.showPreLoader();
                setTimeout(function () {
                    mobileModal.hidePreLoader();
                }, 1000);
            }
        }
    }
</script>

<style scoped>
    @import "../../lib/mobileModal/mobileModal.css";
</style>


